import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { TaskSettingComponent } from './task-setting.component';
import { TaskModifyComponent  } from './task-modify/task-modify.component';
import { TaskAddComponent } from './task-add/task-add.component';

export const routes: Routes = [
    {
        path: '',
        component: TaskSettingComponent,

        children: [
            {
                path: 'task-modify', 
                component: TaskModifyComponent
            },
            {
                path:'task-add',
                component:TaskAddComponent
            }
           
        ]
    }
]
export const TaskSettingRouting: ModuleWithProviders = RouterModule.forChild(routes);