import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Message, SelectItem } from 'primeng/primeng';
import { DropdownModule } from 'primeng/dropdown';
// import { UpdateTaskArrangement } from './bean/ModifyTaskArrangement.bean';
import { UpdateTaskArrangement } from "app/pages/tzb/custom/achievement-task/bean/TaskArrangement.bean";
import { tableMessage } from "app/@uisftech/common/constant/config";
import { AchievementHttpService } from "app/pages/tzb/custom/achievement-task/http/achievement-search.service";
@Component({
	selector: 'task-modify',
	templateUrl: './task-modify.component.html',
	styleUrls: ['./task-modify.component.scss'],
	providers: [Commfunc]
})
export class TaskModifyComponent implements OnInit {
	updatetaskArrangement: UpdateTaskArrangement = new UpdateTaskArrangement();
	@Output() taskModify: EventEmitter<any> = new EventEmitter();//单机事件
	@Input() inValue: any;

	//@Output() motifyClick: EventEmitter<any> = new EventEmitter();
	//表格中无数据显示的内容
	year: string;
	signType: string;
	higherOrgId: string;
	OrganizationName: string;
	OraganOrPersonalNumber: string;
	OraganOrPersonalName: string;
	taskId: string;
	janTask: string;
	febTask: string;
	marTask: string;
	aprTask: string;
	mayTask: string;
	junTask: string;
	julTask: string;
	augTask: string;
	septTask: string;
	octTask: string;
	noveTask: string;
	decTask: string;
	yearTask: number;
	msgs: Message[] = [];
	taskOrgId: string;
	taskUserId: string;
	signType1: string;
	code: any;
	MetricIndex: any;
	taskList: any[] = [];
	constructor(public commfunc: Commfunc, public router: Router, private httpService: AchievementHttpService) {
	}
	ngOnInit() {
		this.codeVal();
		this.MetricIndex = this.code['BdAchievementYe'];//我的业绩
		this.year = this.inValue.year;
		this.signType = this.inValue.signType;
		for (let item of this.MetricIndex) {
			if (this.signType == item.value) {
				this.signType1 = item.label;
			}
		}
		// if (this.signType == "1") {
		// 	this.signType1 = "存款规模"
		// } else if (this.signType == "2") {
		// 	this.signType1 = "贷款规模"
		// } else if (this.signType == "3") {
		// 	this.signType1 = "FTP利润"
		// } else if (this.signType == "4") {
		// 	this.signType1 = "不良贷款"
		// }
		this.higherOrgId = this.inValue.higherOrgId;
		this.OrganizationName = this.inValue.OrganizationName;
		this.OraganOrPersonalNumber = this.inValue.OraganOrPersonalNumber;
		this.OraganOrPersonalName = this.inValue.OraganOrPersonalName;
		this.taskId = this.inValue.taskId;

		this.yearTask = this.inValue.taskList[0].taskAmt;
		this.janTask = this.inValue.taskList[1].taskAmt;
		this.febTask = this.inValue.taskList[2].taskAmt;
		this.marTask = this.inValue.taskList[3].taskAmt;
		this.aprTask = this.inValue.taskList[4].taskAmt;
		this.mayTask = this.inValue.taskList[5].taskAmt;
		this.junTask = this.inValue.taskList[6].taskAmt;
		this.julTask = this.inValue.taskList[7].taskAmt;
		this.augTask = this.inValue.taskList[8].taskAmt;
		this.septTask = this.inValue.taskList[9].taskAmt;
		this.octTask = this.inValue.taskList[10].taskAmt;
		this.noveTask = this.inValue.taskList[11].taskAmt;
		this.decTask = this.inValue.taskList[12].taskAmt;

		this.taskOrgId = this.inValue.taskOrgId;
		this.taskUserId = this.inValue.taskUserId;



	}
	//码值
	codeVal() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	amount() {
		this.yearTask = parseInt(this.janTask) + parseInt(this.febTask) + parseInt(this.marTask) + parseInt(this.aprTask) + parseInt(this.mayTask) + parseInt(this.junTask) + parseInt(this.julTask) + parseInt(this.augTask) + parseInt(this.septTask) + parseInt(this.octTask) + parseInt(this.noveTask) + parseInt(this.decTask);
	}

	formTaskList(obj) {
		let objTemp = {};
		objTemp['taskType'] = obj.taskType;
		objTemp['taskAmt'] = String(obj.taskAmt);
		this.updatetaskArrangement['taskList'].push(objTemp);
	}

	sure() {
		this.updatetaskArrangement['taskUserId'] = this.OraganOrPersonalNumber;
		this.updatetaskArrangement['taskUserName'] = this.OraganOrPersonalName;
		this.updatetaskArrangement['taskOrgId'] = this.higherOrgId;
		this.updatetaskArrangement['taskOrgName'] = this.OraganOrPersonalName;
		this.updatetaskArrangement['signType'] = this.signType;
		this.updatetaskArrangement['taskList'] = [];
		this.updatetaskArrangement.taskId = this.taskId;
		this.updatetaskArrangement.yearTask = this.yearTask + '';
		let arrTemp = [{ taskType: '00', taskAmt: this.yearTask }, { taskType: '01', taskAmt: this.janTask }, { taskType: '02', taskAmt: this.febTask }, { taskType: '03', taskAmt: this.marTask }, { taskType: '04', taskAmt: this.aprTask }, { taskType: '05', taskAmt: this.mayTask }, { taskType: '06', taskAmt: this.junTask }, { taskType: '07', taskAmt: this.julTask }, { taskType: '08', taskAmt: this.augTask }, { taskType: '09', taskAmt: this.septTask }, { taskType: '10', taskAmt: this.octTask }, { taskType: '11', taskAmt: this.noveTask }, { taskType: '12', taskAmt: this.decTask }]
		arrTemp.forEach(element => {
			this.formTaskList(element);
		});
		this.httpService.updateTaskArrangement(this.updatetaskArrangement).subscribe(data => {
			if (data.returnCode.code == "success") {
				let param = {
					year: this.year,
					signType: this.signType,
					OraganOrPersonalNumber: this.OraganOrPersonalNumber,
					OraganOrPersonalName: this.OraganOrPersonalName,
					taskOrgId: this.taskOrgId,
					taskUserId: this.taskUserId,
				}
				this.taskModify.emit(param);

				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})

	}
}